import { useEffect, useCallback } from 'react';
import { analytics, trackingEvents } from '../services/analytics';

export const useAnalytics = () => {
  useEffect(() => {
    analytics.trackPageView(window.location.pathname);
  }, []);

  const trackEvent = useCallback((category: string, action: string, label?: string, value?: number) => {
    analytics.trackEvent({ category, action, label, value });
  }, []);

  const trackScrollDepth = useCallback(() => {
    let maxScroll = 0;
    const handleScroll = () => {
      const scrollHeight = document.documentElement.scrollHeight - window.innerHeight;
      const scrollPercent = Math.round((window.scrollY / scrollHeight) * 100);
      
      if (scrollPercent > maxScroll) {
        maxScroll = scrollPercent;
        if (scrollPercent === 25 || scrollPercent === 50 || scrollPercent === 75 || scrollPercent === 100) {
          analytics.trackScrollDepth(scrollPercent);
        }
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const trackTimeOnPage = useCallback(() => {
    const startTime = Date.now();
    
    return () => {
      const timeSpent = Math.round((Date.now() - startTime) / 1000);
      analytics.trackTimeOnPage(timeSpent);
    };
  }, []);

  const { trackEvent: _, ...otherTrackingEvents } = trackingEvents;
  
  return {
    trackEvent,
    trackScrollDepth,
    trackTimeOnPage,
    ...otherTrackingEvents,
  };
};