import axios from 'axios';
import type { ApiResponse, WaitlistEntry } from '../types';

const API_BASE_URL = import.meta.env.VITE_API_URL || 'http://localhost:8000/api';

const api = axios.create({
  baseURL: API_BASE_URL,
  headers: {
    'Content-Type': 'application/json',
  },
  timeout: 10000,
});

api.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response) {
      console.error('API Error:', error.response.data);
    } else if (error.request) {
      console.error('Network Error:', error.request);
    } else {
      console.error('Error:', error.message);
    }
    return Promise.reject(error);
  }
);

export const waitlistApi = {
  join: async (data: WaitlistEntry): Promise<ApiResponse<{ position: number; email: string }>> => {
    const response = await api.post('/waitlist/join', data);
    return response.data;
  },

  getPosition: async (email: string): Promise<ApiResponse<{ position: number }>> => {
    const response = await api.get(`/waitlist/position/${email}`);
    return response.data;
  },
};

export const analyticsApi = {
  trackEvent: async (eventType: string, eventData: Record<string, any>): Promise<void> => {
    await api.post('/analytics/event', {
      eventType,
      eventData,
      timestamp: new Date().toISOString(),
    });
  },

  trackConversion: async (conversionType: string, value?: number): Promise<void> => {
    await api.post('/analytics/conversion', {
      conversionType,
      value,
      timestamp: new Date().toISOString(),
    });
  },
};

export const emailApi = {
  verify: async (email: string): Promise<ApiResponse<{ token: string }>> => {
    const response = await api.post('/email/verify', { email });
    return response.data;
  },

  confirmToken: async (token: string): Promise<ApiResponse<{ success: boolean }>> => {
    const response = await api.get(`/email/confirm/${token}`);
    return response.data;
  },
};

export default api;