import ReactGA from 'react-ga4';
import { analyticsApi } from './api';

export interface AnalyticsEvent {
  category: string;
  action: string;
  label?: string;
  value?: number;
}

class AnalyticsService {
  private isInitialized = false;

  init(measurementId: string) {
    if (!this.isInitialized && measurementId) {
      ReactGA.initialize(measurementId);
      this.isInitialized = true;
    }
  }

  trackPageView(path: string) {
    if (this.isInitialized) {
      ReactGA.send({ hitType: 'pageview', page: path });
    }
  }

  trackEvent({ category, action, label, value }: AnalyticsEvent) {
    if (this.isInitialized) {
      ReactGA.event({
        category,
        action,
        label,
        value,
      });
    }

    // Also send to our backend
    analyticsApi.trackEvent(`${category}_${action}`, {
      label,
      value,
    }).catch(console.error);
  }

  trackConversion(type: string, value?: number) {
    this.trackEvent({
      category: 'Conversion',
      action: type,
      value,
    });

    analyticsApi.trackConversion(type, value).catch(console.error);
  }

  trackScrollDepth(percentage: number) {
    this.trackEvent({
      category: 'Engagement',
      action: 'Scroll',
      label: `${percentage}%`,
      value: percentage,
    });
  }

  trackTimeOnPage(seconds: number) {
    this.trackEvent({
      category: 'Engagement',
      action: 'Time on Page',
      value: seconds,
    });
  }

  trackCTAClick(ctaName: string, location: string) {
    this.trackEvent({
      category: 'CTA',
      action: 'Click',
      label: `${ctaName} - ${location}`,
    });
  }

  trackFormSubmission(formName: string, success: boolean) {
    this.trackEvent({
      category: 'Form',
      action: success ? 'Submit Success' : 'Submit Error',
      label: formName,
    });
  }

  trackVideoPlay(videoName: string, duration?: number) {
    this.trackEvent({
      category: 'Video',
      action: 'Play',
      label: videoName,
      value: duration,
    });
  }

  trackSocialShare(platform: string, content: string) {
    this.trackEvent({
      category: 'Social',
      action: 'Share',
      label: `${platform} - ${content}`,
    });
  }
}

export const analytics = new AnalyticsService();

// Predefined event tracking functions
export const trackingEvents = {
  heroCtaClick: () => analytics.trackCTAClick('Get Early Access', 'Hero'),
  watchDemoClick: () => analytics.trackCTAClick('Watch Demo', 'Hero'),
  pricingCtaClick: () => analytics.trackCTAClick('Get Instant Access', 'Pricing'),
  finalCtaClick: () => analytics.trackCTAClick('Claim Early Access', 'Final CTA'),
  waitlistJoin: (position: number) => analytics.trackConversion('Waitlist Join', position),
  paymentInitiated: () => analytics.trackConversion('Payment Initiated'),
  paymentCompleted: (amount: number) => analytics.trackConversion('Payment Completed', amount),
  faqOpen: (question: string) => analytics.trackEvent({
    category: 'FAQ',
    action: 'Open',
    label: question,
  }),
  trackEvent: (category: string, action: string, label?: string, value?: number) => analytics.trackEvent({ category, action, label, value }),
  trackFormSubmission: (formName: string, success: boolean) => analytics.trackFormSubmission(formName, success),
};