import { useState, useCallback } from 'react';
import { stripeConfig, openStripePayment } from '../services/stripe';
import { useAnalytics } from './useAnalytics';

export const useStripePayment = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const { paymentInitiated } = useAnalytics();

  const initiatePayment = useCallback(async (priceType: string = 'earlyBird') => {
    setIsLoading(true);
    setError(null);

    try {
      // Track payment initiation
      paymentInitiated();

      // Get the appropriate payment link
      const paymentLink = stripeConfig.paymentLinks[priceType];

      if (!paymentLink || paymentLink === '#') {
        throw new Error('Payment link not configured. Please try again later.');
      }

      // Open Stripe payment link in new tab
      openStripePayment(paymentLink);

      setIsLoading(false);
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'Failed to initiate payment';
      setError(errorMessage);
      setIsLoading(false);
      console.error('Payment error:', err);
    }
  }, [paymentInitiated]);

  const clearError = useCallback(() => {
    setError(null);
  }, []);

  return {
    initiatePayment,
    isLoading,
    error,
    clearError,
  };
};