import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import { useEffect } from 'react';
import ReactGA from 'react-ga4';
import { ThemeProvider } from './contexts/ThemeContext';
import Header from './components/layout/Header';
import CookieBanner from './components/common/CookieBanner';
import Hero from './components/landing/Hero';
import ValueProposition from './components/landing/ValueProposition';
import Features from './components/landing/Features';
import HowItWorks from './components/landing/HowItWorks';
import Pricing from './components/landing/Pricing';
import ReferralProgram from './components/landing/ReferralProgram';
import Testimonials from './components/landing/Testimonials';
import FAQ from './components/landing/FAQ';
import CTA from './components/landing/CTA';
import Footer from './components/landing/Footer';

const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      staleTime: 60 * 1000,
      retry: 1,
    },
  },
});

function App() {
  useEffect(() => {
    // Google Analytics initialization is now handled by CookieBanner based on consent
    const consent = localStorage.getItem('cookieConsent');
    if (consent) {
      const consentData = JSON.parse(consent);
      if (consentData.analytics && import.meta.env.VITE_GA_MEASUREMENT_ID) {
        ReactGA.initialize(import.meta.env.VITE_GA_MEASUREMENT_ID);
        ReactGA.send({ hitType: 'pageview', page: window.location.pathname });
      }
    }
  }, []);

  return (
    <ThemeProvider>
      <QueryClientProvider client={queryClient}>
        <div className="min-h-screen bg-white dark:bg-gray-900 transition-colors duration-200">
          <Header />
          <Hero />
          <ValueProposition />
          <Features />
          <HowItWorks />
          <Testimonials />
          <Pricing />
          <ReferralProgram />
          <FAQ />
          <CTA />
          <Footer />
          <CookieBanner />
        </div>
      </QueryClientProvider>
    </ThemeProvider>
  );
}

export default App;