import { loadStripe } from '@stripe/stripe-js';

const stripePublishableKey = import.meta.env.VITE_STRIPE_PUBLISHABLE_KEY;

let stripePromise: Promise<any> | null = null;

export const getStripe = () => {
  if (!stripePromise && stripePublishableKey) {
    stripePromise = loadStripe(stripePublishableKey);
  }
  return stripePromise;
};

export const stripeConfig = {
  paymentLinks: {
    earlyBird: import.meta.env.VITE_STRIPE_EARLY_BIRD_LINK || '#',
    regular: import.meta.env.VITE_STRIPE_REGULAR_LINK || '#',
    'single-startup': import.meta.env.VITE_STRIPE_SINGLE_STARTUP_LINK || '#',
    'startup-trio': import.meta.env.VITE_STRIPE_STARTUP_TRIO_LINK || '#',
    'startup-empire': import.meta.env.VITE_STRIPE_STARTUP_EMPIRE_LINK || '#',
  } as Record<string, string>,
  
  customerPortalUrl: import.meta.env.VITE_STRIPE_CUSTOMER_PORTAL_URL || '#',
};

export const openStripePayment = (paymentLinkUrl: string) => {
  if (paymentLinkUrl && paymentLinkUrl !== '#') {
    window.open(paymentLinkUrl, '_blank');
  } else {
    console.error('Stripe payment link not configured');
  }
};

export const openCustomerPortal = async (customerId?: string) => {
  if (customerId && stripeConfig.customerPortalUrl !== '#') {
    const portalUrl = `${stripeConfig.customerPortalUrl}?prefilled_email=${customerId}`;
    window.open(portalUrl, '_blank');
  } else {
    console.error('Customer portal not available');
  }
};